<template>
  <v-app>
    <v-main >
      <v-row style="height:100%" > 
        <v-col align="center">
          <v-card style="height:900px"  flat> 
            <v-card-title style="place-content: center;"> showImage </v-card-title>
              <v-file-input
                style="width:600px"
                accept="image/*"
                label="File input"
                @change="previewImage"
              ></v-file-input>
              <v-row justify="center" >
                <v-col cols="8">
                  <v-img id="cardcanvas" class="login-description" outlined  v-if="show.img" :src="imageCheck(show)" contain>
                  </v-img>
                </v-col>  
              </v-row>
              <v-btn @click="($router.go(-1))" color="blue" dark class="mt-6" width="50%">
                <v-icon>
                  mdi-arrow-u-left-bottom
                </v-icon>
              </v-btn>
          </v-card>
        </v-col>
      </v-row>
    </v-main>
  </v-app>
</template>

<script>

  export default {
    name: 'ShowFabric',
    data: () => ({
      picture:"",
      show:{
        img:""
      },
    }),
    // created() {
      
    // },
    methods:{
      previewImage(event){
          this.show.img =URL.createObjectURL(event);

      },
      imageCheck(show) {
        return show.img;
      },
    },
  }
</script>
